.splash{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    grid-gap: 2vh;
    justify-content: center;
    align-items: center;
    box-shadow: 1px 7px 5px var(--font-color);
    background: rgba(255, 250, 240, 0);
    font-size:calc(16px + 6 * ((100vw - 320px) / 680));
    background-repeat: no-repeat;
    color: antiquewhite;
    background-position: right;
    height: 90vh;
    padding:1%;
    background-attachment: fixed;
    background-size:cover;
}
.pentagon {
      position: relative;
      width: 54px;
      box-sizing: content-box;
      border-width: 50px 18px 0;
      border-style: solid;
      border-color: #a62e5ba8 transparent;
      transform: rotate(92deg);
    }
.pentagon:before {
      content: "";
      position: absolute;
      height: 0;
      width: 0;
      top: -85px;
      left: -18px;
      border-width: 0 45px 35px;
      border-style: solid;
      border-color: #663648 transparent
    }
#explore{
    background: none;
    border-radius: 100px;
    border: 1px solid white;
    padding: 10px;
    padding-left: 30px;
    padding-right: 32px;
    color: white;
    font-size: calc(16px + 6 * ((100vw - 320px) / 680));
    text-decoration: none;
    text-shadow: none;
    transition:background ease-out 0.3s;

}
#explore:hover{
  cursor:pointer;
  background: whitesmoke;
  color:var(--font-color);
  transition: background ease-in 0.3s;
}
#explore:focus{
  outline: none;
  transition: none;
}

@media only screen and (max-width: 600px) {

  .splash{
    height:60vh !important;
       background-attachment: unset !important;
    
  }
}


#footer{
    text-align: center;
    background-color: whitesmoke;
    padding: 5px;
}
#footer > *{
        margin: 0;
}

.about{
    display:flex;
    align-items: center;
    justify-content: center;
    grid-gap:2vw;
    padding:5%;
    box-shadow: 1px 2px 6px grey;
    background: white;
    border-bottom: 1px solid #8080809c;
}
.about > div:nth-child(1){
    flex: 3 1;
}
.about > div:nth-child(2){
    flex: 1 1;

}
.about > div:nth-child(1) > p {
    font-size:calc(16px + 6 * ((100vw - 320px) / 680));
}
.about > div:nth-child(2) > img {
    border-radius: 116px;
    border-top: 4px solid #f941418f;
    border-right: 4px solid #f941418f;
    border-left: 4px solid #f941418f;
}



#Projects{
    display:grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    padding:0% 5% 3% 5%;
    grid-gap:2vw;
    box-shadow: 1px 4px 7px grey;
    background: whitesmoke;
}
#Projects >div {
    margin:0 auto 0 auto;
    text-align: center;
    padding: 1%;
}


#Projects >div >*{
    /* font-size:var(--responsive-font); */
    width:100%;
    font-size: 2rem;
}
#Projects >div >img{
   border-radius: 6px;
   box-shadow:3px 7px 4px grey;
}
#ProjectLinks{
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap:1vw;
    margin-top: 1%;
}
#ProjectLinks >button{
    outline: none;
    padding:10px;
    border-radius: 5px;
    font-size: 16px;
    box-shadow: 1px 2px 3px #a0013b;
}
#ProjectLinks >button:nth-child(1){
    color:white;
    background-color: #000000f0;
}
#ProjectLinks >button:hover{
   background: linear-gradient(45deg, #210130, #650125);
   cursor: pointer;
}
#ProjectLinks >button:nth-child(2):hover{
    color:white;
    /* background-color: #000000f0; */
}
#Contact{
    display:flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
    grid-gap: 1vw;
    background: #0000005e;
}
#Contact >div:nth-child(1){
    background:rgb(255 255 255 / 90%);
    padding:2%;
    box-shadow:1px 3px 4px grey;
    min-width:35%;
    border-radius:5px;
    margin:3%;
}
#Contact >div > h2{
    font-size: 3rem;
    margin:0;
    border-bottom: 1px solid grey;
    margin-bottom: 2%;
    text-align: center;
}
#Contact >div >div{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 1vw;
    max-width: 700px;
}
#Contact >div > div > *:nth-child(1){
    flex:1 1;
    min-width: 60px;
}
#Contact >div > div > *:nth-child(2){
    border-radius: 5px;
    border: 3px solid rgba(128, 128, 128, 0.644);
    padding: 1%;
    font-size: 20px;
    flex: 8 1;
}
